import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

class Geometry extends React.Component {
  init = () => {};

  componentDidMount() {}

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="geometry">
        <main>
          <h2>
            <x-message>Geometry</x-message>
          </h2>
          <div className="geometry-content">
            <h3>
              <x-message>Object</x-message>
            </h3>
            <section id="position-section">
              <x-box id="x-box" vertical="">
                <h4 id="x-heading">
                  <x-message>X</x-message>
                </h4>
                <x-numberinput
                  id="x-input"
                  value="263.7"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
              <x-box id="y-box" vertical="">
                <h4 id="y-heading">
                  <x-message>Y</x-message>
                </h4>
                <x-numberinput
                  id="y-input"
                  value="86.2"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </section>
            <section id="position-section">
              <x-box id="x-box" vertical="">
                <h4 id="x-heading">
                  <x-message>width</x-message>
                </h4>
                <x-numberinput
                  id="x-input"
                  value="263.7"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
              <x-box id="y-box" vertical="">
                <h4 id="y-heading">
                  <x-message>height</x-message>
                </h4>
                <x-numberinput
                  id="y-input"
                  value="86.2"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </section>
          </div>
        </main>
      </div>
    );
  };
}

export default Geometry;
