import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

class Fill extends React.Component {
  init = () => {};

  componentDidMount() {}

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="fill">
        <h2>
          <x-message href="#fill">Fill</x-message>
        </h2>
        <section id="paint-section">
          <h3 id="section-heading">
            <x-message href="#paint">Paint</x-message>
          </h3>
          <div>
            <x-rectcolorpicker
              value="#bada55"
              alphaslider=""
            ></x-rectcolorpicker>
          </div>
        </section>
        <hr />
        <div id="opacity-section">
          <h3 id="opacity-section-heading">
            <x-message href="#opacity">Opacity</x-message>
          </h3>
          <div className="box">
            <x-slider
              id="opacity-slider"
              value="1"
              min="0"
              max="1"
              step="0.01"
            ></x-slider>
            <x-numberinput
              id="opacity-input"
              value="0.37"
              min="0"
              max="1"
              step="0.01"
              condensed=""
              role="input"
              aria-disabled="false"
              tabindex="0"
              hasstepper=""
            >

              <x-stepper></x-stepper>
            </x-numberinput>
          </div>
        </div>
      </div>
    );
  };
}

export default Fill;
