import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

interface State {
  fonts: Array<any>;
}

interface Props {}

class Typography extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      fonts: [],
    };
  }

  queryLocalFonts = async () => {
    const fonts = await (window as any).queryLocalFonts();
    this.setState({
      fonts,
    });
    console.log(fonts)
    console.log(this.state.fonts)
  };
  
  componentDidMount() {}
  componentWillUnmount() {}

  render = () => {
    return (
      <div id="typography">
        <main>
          <h2>
            <x-message>typography</x-message>
          </h2>
          <section id="font-section">
            <h3 id="font-section-heading">
              <x-message href="#font">Font</x-message>
            </h3>
            <x-box id="font-family-subsection" vertical="">
              <h4 id="font-family-heading">
                <x-message href="#font.family">Family</x-message>
              </h4>

              <x-select onClick={() => this.queryLocalFonts()}>
                <x-menu>
                {this.state.fonts.map((item, index) => (
                  <x-menuitem key={index} value={item.family}>
                    <x-label>Arial</x-label>
                  </x-menuitem>
                ))}
                </x-menu>
              </x-select>
            </x-box>
            <x-box id="font-face-subsection" vertical="">
              <h4 id="font-face-heading">
                <x-message href="#font.face">Face</x-message>
              </h4>
              <x-box id="font-face-box">
                <x-buttons>
                  <x-button>Bold</x-button>
                  <x-button>Italic</x-button>
                </x-buttons>
              </x-box>
            </x-box>
            <x-box id="font-size-subsection" vertical="">
              <h4 id="font-size-heading">
                <x-message>Size</x-message>
              </h4>
              <x-box id="font-size-box">
                <x-slider
                  id="font-size-slider"
                  value="13"
                  min="1"
                  max="150"
                  step="1"
                  aria-disabled="false"
                  tabindex="0"
                ></x-slider>
                <x-numberinput
                  id="font-size-input"
                  value="13"
                  min="1"
                  step="1"
                  condensed=""
                  role="input"
                  aria-disabled="true"
                  tabindex="-1"
                  hasstepper=""
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </x-box>
          </section>
          <hr />
          <section id="text-section">
            <h3 id="text-section-heading" disabled="">
              <x-message href="#text">Text</x-message>
            </h3>
            <x-box id="text-decoration-box" vertical="">
              <h4 id="text-decoration-heading" disabled="">
                <x-message href="decoration">Decoration</x-message>
              </h4>
              <x-buttons id="text-decoration-buttons">
                <x-button
                  id="underline-button"
                  value="underline"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-decoration.underline">
                      Underline
                    </x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="line-through-button"
                  value="line-through"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-decoration.line-through">
                      Line-Through
                    </x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="overline-button"
                  value="overline"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-decoration.overline">
                      Overline
                    </x-message>
                  </x-label>
                </x-button>
              </x-buttons>
            </x-box>
            <x-box id="text-transform-box" vertical="">
              <h4 id="text-transform-heading" disabled="">
                <x-message href="#transform">Transform</x-message>
              </h4>
              <x-buttons id="text-transform-buttons">
                <x-button
                  id="capitalize-button"
                  value="capitalize"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-transform.capitalize">
                      Capitalize
                    </x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="uppercase-button"
                  value="uppercase"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-transform.uppercase">
                      Uppercase
                    </x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="lowercase-button"
                  value="lowercase"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-transform.lowercase">
                      Lowercase
                    </x-message>
                  </x-label>
                </x-button>
              </x-buttons>
            </x-box>
            <x-box id="text-anchor-box" vertical="">
              <h4 id="text-anchor-heading" disabled="">
                <x-message href="#anchor">Anchor</x-message>
              </h4>
              <x-buttons id="text-anchor-buttons">
                <x-button
                  id="start-text-anchor-button"
                  value="start"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-anchor.start">Start</x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="middle-text-anchor-button"
                  value="middle"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-anchor.middle">Middle</x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="end-text-anchor-button"
                  value="end"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-anchor.end">End</x-message>
                  </x-label>
                </x-button>
              </x-buttons>
            </x-box>
          </section>
          <hr />
          <section id="spacing-section">
            <h3 id="spacing-section-heading" disabled="">
              <x-message href="#spacing">Spacing</x-message>
            </h3>
            <x-box id="letter-spacing-box" vertical="">
              <h4 id="letter-spacing-heading" disabled="">
                <x-message href="#spacing.letter">Letter</x-message>
              </h4>
              <x-box id="letter-spacing-inner-box">
                <x-slider
                  id="letter-spacing-slider"
                  value="0"
                  min="-20"
                  max="20"
                  step="0.1"
                  aria-disabled="false"
                  tabindex="0"
                  disabled=""
                ></x-slider>
                <x-numberinput
                  id="letter-spacing-input"
                  value="0"
                  step="0.1"
                  condensed=""
                  role="input"
                  aria-disabled="true"
                  tabindex="-1"
                  hasstepper=""
                  disabled=""
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </x-box>
            <x-box id="word-spacing-box" vertical="">
              <h4 id="word-spacing-heading" disabled="">
                <x-message href="#spacing.word">Word</x-message>
              </h4>
              <x-box id="word-spacing-inner-box">
                <x-slider
                  id="word-spacing-slider"
                  value="0"
                  min="-100"
                  max="100"
                  step="0.1"
                  aria-disabled="false"
                  tabindex="0"
                  disabled=""
                ></x-slider>
                <x-numberinput
                  id="word-spacing-input"
                  value="0"
                  step="0.1"
                  condensed=""
                  role="input"
                  aria-disabled="true"
                  tabindex="-1"
                  hasstepper=""
                  disabled=""
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </x-box>
            <x-box id="line-spacing-box" vertical="">
              <h4 id="line-spacing-heading" disabled="">
                <x-message href="#spacing.line">Line</x-message>
              </h4>
              <x-box id="line-spacing-inner-box">
                <x-slider
                  id="line-spacing-slider"
                  value="0"
                  min="-3"
                  max="3"
                  step="0.1"
                  aria-disabled="false"
                  tabindex="0"
                  disabled=""
                ></x-slider>
                <x-numberinput
                  id="line-spacing-input"
                  value="0"
                  step="0.1"
                  condensed=""
                  role="input"
                  aria-disabled="true"
                  tabindex="-1"
                  hasstepper=""
                  disabled=""
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </x-box>
          </section>
        </main>
      </div>
    );
  };
}

export default Typography;
