import "./index.css";
import React from "react";
import { Stage } from "../../lux";

interface State {
  stage: Stage | undefined;
}

interface Props {}

class Menus extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      stage: void 0,
    };
  }

  action = (action: string) => {
    const stage = (window as any).stage as Stage;
    switch (action) {
      case "undo":
        stage.history?.undo();
        break;
      case "redo":
        stage.history?.redo();
        break;
      case "cut":
        stage.clipboard?.cut();
        break;
      case "copy":
        stage.clipboard?.copy();
        break;
      case "paste":
        stage.clipboard?.paste();
        break;
    }
  };

  componentDidMount() {}
  componentWillUnmount() {}

  render() {
    return (
      <header id="header">
        <x-menubar>
          <x-menuitem>
            <x-label>File</x-label>

            <x-menu>
              <x-menuitem>
                <x-label>New</x-label>
              </x-menuitem>

              <x-menuitem>
                <x-label>Open</x-label>
              </x-menuitem>

              <hr />

              <x-menuitem>
                <x-label>Save</x-label>
              </x-menuitem>

              <x-menuitem>
                <x-label>Save as…</x-label>
              </x-menuitem>
            </x-menu>
          </x-menuitem>

          <x-menuitem>
            <x-label>Edit</x-label>

            <x-menu>
              <x-menuitem onClick={() => this.action("undo")}>
                <x-label>Undo</x-label>
                <x-shortcut value="Control+Z"></x-shortcut>
              </x-menuitem>

              <x-menuitem onClick={() => this.action("redo")}>
                <x-label>Redo</x-label>
                <x-shortcut value="Control+Shift+Z"></x-shortcut>
              </x-menuitem>

              <hr />

              <x-menuitem onClick={() => this.action("cut")}>
                <x-label>Cut</x-label>
                <x-shortcut value="Control+X"></x-shortcut>
              </x-menuitem>

              <x-menuitem onClick={() => this.action("copy")}>
                <x-label>Copy</x-label>
                <x-shortcut value="Control+C"></x-shortcut>
              </x-menuitem>

              <x-menuitem onClick={() => this.action("paste")}>
                <x-label>Paste</x-label>
                <x-shortcut value="Control+V"></x-shortcut>
              </x-menuitem>
            </x-menu>
          </x-menuitem>

          <x-menuitem>
            <x-label>View</x-label>

            <x-menu>
              <x-menuitem toggled="true">
                <x-label>Maunal Guides</x-label>
              </x-menuitem>

              <x-menuitem toggled="true">
                <x-label>Smart Guides</x-label>
              </x-menuitem>
            </x-menu>
          </x-menuitem>

          <x-menuitem>
            <x-label>Help</x-label>

            <x-menu>
              <x-menuitem>
                <x-label>wechat:13696977423</x-label>
              </x-menuitem>
            </x-menu>
          </x-menuitem>
        </x-menubar>
      </header>
    );
  }
}

export default Menus;
